import { createWebHashHistory, createRouter } from "vue-router";
import MainPage from '@/views/MainPage.vue';
import EunaPage from '@/views/EunaPage.vue';
import YeopPage from '@/views/YeopPage.vue';

/**
 * path : route를 찾을 수 있는 url path
 * name : route로 연결할 때 사용하는 이름 (선택 사항)
 * component : route에서 불러와질 컴포넌트
 * props :  path부분의 :name을 props로 전달 (선택 사항)
 */
const routes =[
    // path별 component를 추가
    // { path : "/user/:name", name : "User", component : User },
    // { path : "/user2/:name", name : "User2", component : User2, props: true },
    { 
        path : '/main',
        component: () => MainPage, 
    },
    { 
        path : '/euna',
        component: () => EunaPage, 
    },
    { 
        path : '/yeop',
        component: () => YeopPage, 
    },
]

const router = createRouter({
    history : createWebHashHistory('/vue/'),
    routes
});


export default router;
